import './App.css';
import PieceOfArt from './PieceOfArt.js';
import { Component } from 'react';
import axios from 'axios';

class App extends Component {
  constructor() {
    super();
    // console.log('Construct itttt');

    this.state = {
      message: 'Because art gives meaning to the life cycles when using GH Pages in React',
      allTheArt: []
    }
  }

  componentDidMount() {
    // console.log('Oh, now I have a new lifecycle to hook into.')
    //Gonna hit the API in here
    this.callAPI();
    
  }

  callAPI = () => {
    let apiKey = "6Tzgm6OX";
    let apiUrl = "https://www.rijksmuseum.nl/api/en/collection";
    axios({
      url: apiUrl,
      method: 'GET',
      responseType: 'json',
      params: {
        key: apiKey,
        q: 'computer'
      }
    }
    )
      .then( (res) => {
        // console.log(res.data.artObjects);
        let art = res.data.artObjects;
        this.setState({
          allTheArt: art,
        });
        // console.log(this.state.allTheArt);
      });
  }

  

  componentDidUpdate() {
    // console.log('My component is a new component now...')
  }

  render() {
    // console.log('Render itttt');
    return (
      <div className="App">
        <h1>Some more dutch art </h1>
        
        <p>{this.state.message}</p>
        {
          this.state.allTheArt.length > 0
            ? this.state.allTheArt.map((art) => {
              // console.log(art);
            return (
              
              <PieceOfArt
                key={art.id}
                longTitle={art.longTitle}
                title={art.title}
                url={art.webImage.url}
                principalOrFirstMaker={art.principalOrFirstMaker}
              />
            )
          })
          : <h3>Getting some art...</h3>
        }
      </div>
    );
  }
}

export default App;
